import ShareYourFeedbackContent from "./ShareYourFeedbackContent";
import ThankYou from "./ThankYou";
import React from "react";

export const ShareYourFeedbackContext = React.createContext();

export default function ShareYourFeedback() {
  const [showThankYouContent, setShowThankYouContent] = React.useState(false);

  return (
    <ShareYourFeedbackContext.Provider
      value={{ showThankYouContent, setShowThankYouContent }}
    >
      {!showThankYouContent ? <ShareYourFeedbackContent /> : <ThankYou />}{" "}
    </ShareYourFeedbackContext.Provider>
  );
}
