import { Box, Button, Typography, makeStyles } from "@material-ui/core";
import CongratulationV2Image from "../../../assets/img/thank-you.svg";

const useStyles = makeStyles({
  root: {
    animation: "fadeIn .5s ease-in-out",
    backgroundColor: "#FFFF",
    height: "100%",
    marginTop: "0",
    zoom: 1.2,
  },
});

export const ThankYou = () => {
  const classes = useStyles();
  const thankyou_message = "Thank you for your feedback!";
  const close_message = "Close";

  return (
    <Box>
      <Box
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          marginTop: "16px",
        }}
      >
        <img src={CongratulationV2Image} alt="Congratulation" />
        <Typography
          style={{
            fontSize: "14px",
            fontWeight: 500,
            fontFamily: "Inter",
            marginTop: "16px",
          }}
        >
          {thankyou_message}
        </Typography>
      </Box>
    </Box>
  );
};

export default ThankYou;
