import {
  Box,
  Button,
  ThemeProvider,
  Typography,
  createTheme,
} from "@material-ui/core";
import Rating from "@material-ui/lab/Rating";
import StarBorderIcon from "@material-ui/icons/StarBorder";
import TextField from "@material-ui/core/TextField";
import { ShareYourFeedbackContext } from "./ShareYourFeedback";
import React from "react";
import KSActionService from "../../services/KSActionService";

export const ShareYourFeedbackContent = () => {
  const text = "How did StudioAssist work out for your testing?";
  const textInFeedbackBox = "Tell us what you think...";
  const buttonTitle = "Send Feedback";

  const {setShowThankYouContent} = React.useContext(ShareYourFeedbackContext);
  const [feedbackText, setFeedbackText] = React.useState('');
  const [numStars, setNumStars] = React.useState(5);

  const handleFeedbackInput = (event) => {
    setFeedbackText(event.target.value);
  }

  const handleRatingInput =  (event, value) => {
    if (!value) {
      value = 0;
    }
    setNumStars(value);
  }

  const handleClick = () => {
    setShowThankYouContent(true);
    KSActionService.sendStudioAssistFeedback(numStars, feedbackText);
  }

  const textTheme = createTheme({
    typography: {
      fontFamily: "Inter",
    },
  });

  return (
    <Box>
      <Box
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          marginTop: "8px",
        }}
      >
        <ThemeProvider theme={textTheme}>
          <Typography style={{ fontWeight: 500, fontSize: "14px" }}>
            {text}
          </Typography>
          <Rating
            name="studioassist-rating"
            defaultValue={5}
            style={{
              width: "25px",
              height: "23.75px",
              marginRight: "120px",
              marginTop: "8px",
            }}
            emptyIcon={<StarBorderIcon fontSize="inherit" />}
            size="large"
            onChange={handleRatingInput}
          />
        </ThemeProvider>
      </Box>

      <Box
        style={{
          display: "flex",
          flexDirection: "column",
          marginTop: "20px",
        }}
      >
        <TextField
          multiline
          variant="outlined"
          placeholder={textInFeedbackBox}
          minRows={4}
          maxRows={4}
          sx={{ justifyContent: "center" }}
          InputProps={{
            style: {
              fontFamily: "Inter",
            },
          }}
          onChange={handleFeedbackInput}
        />
        <Box
          style={{
            alignSelf: "flex-end",
          }}
        >
          <Button
            variant="contained"
            className="send-feedback"
            disableElevation
            color="primary"
            style={{
              textTransform: "none",
              borderRadius: "4px",
              width: "120px",
              height: "25px",
              marginTop: "16px",
            }}
            onClick={handleClick}
          >
            <Typography
              style={{
                fontFamily: "Roboto",
                fontSize: "12px",
                fontWeight: 500,
              }}
              variant="h4"
            >
              {buttonTitle}
            </Typography>
          </Button>
        </Box>
      </Box>
    </Box>
  );
};

export default ShareYourFeedbackContent;